@import 'include-media';
@import "normalize/import-now";
@import 'bootstrap-grid';
@import 'colors';
@import 'typography';
@import 'buttons';
@import 'structure';
@import 'forms';

body{
    background-color: $cinza1;
}

body.login{
    
    .container-fluid{
        padding-left: 0;
        padding-right: 0;
        .row{
            margin-left: 0;
            margin-right: 0;
        }
        .col-md-7, .col-md-5{
            padding-left: 0;
            padding-right: 0;
        }
    }
    .block{
        height: 100vh;
        margin-bottom: 0;
        display: flex;
        align-items: center;
    }
    .bg-login{
        background-image: url('../images/bg_login.jpg');
        background-size: cover;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: center;
        padding-right: 5% !important;
        h1, p{
            color: white;
        }
        h1{
            font-family: 'Poppins_Bold';
            font-size: 48px;
        }
        p{
            font-size: 13px;
            text-align: right;
            max-width: 160px;
        }
    }
    form{
        text-align: left;
        h2{
            color: $azul1;
        }
    }
    .logo{
        margin-bottom: 50px;
    }
    .change-password{
        margin-top: 150px;
    }
}