@import 'colors';

form{
    .form-group{
        margin-bottom: 15px;
        &.flex{
            display: flex;
            align-items: center;
            width: 100%;
            &.space-between{
                justify-content: space-between;
            }
        }
    }
    .invalid-feedback{
        display: none;
        font-size: .928571rem;
        margin-top: .25rem;
        color: $vermelho1;
        text-align: right;
    }
    input[type=text], input[type=number], input[type=password], input[type=email], {
        display: block;
        width: 100%;
        padding: 10px 15px;
        font-size: 1rem;
        line-height: 16px;
        color: $cinza6;
        background-color: #fff;
        background-clip: padding-box;
        border: 1px solid #dcdfe3;
        border-radius: .25rem;
        transition: border-color ease-in-out .15s;
        &.is-valid{
            border-color: $verde1;
        }
        &.is-invalid{
            border-color: $vermelho1;
            &~.invalid-feedback{
                display: block;
            }
        }
    }
}