@import 'colors';

.btn{
    font-family: 'Poppins_Regular';
    display: inline-block;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    padding: 8px 14px;
    font-size: 1rem;
    line-height: 16px;
    border-radius: .25rem;
    text-decoration: none;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;

    &:not(:disabled):not(.disabled) {
        cursor: pointer;
    }

    // DEFAULT BUTTONS --------------------------------

    &-primary{
        color: #fff;
        background-color: $azul2;
        border-color: $azul3;

        &:hover{
            color: #fff;
            background-color: $azul3;
            border-color: $azul4;
        }
    }

    &-secondary{
        color: #212529;
        background-color: $cinza2;
        border-color: $cinza3;

        &:hover{
            color: #212529;
            background-color: $cinza3;
            border-color: $cinza4;
        }
    }

    &-success{
        color: #fff;
        background-color: $verde1;
        border-color: $verde2;

        &:hover{
            color: #fff;
            background-color: $verde2;
            border-color: $verde3;
        }
    }

    &-info{
        color: #fff;
        background-color: $azul5;
        border-color: $azul6;

        &:hover{
            color: #fff;
            background-color: $azul6;
            border-color: $azul7;
        }
    }

    &-warning{
        color: #fff;
        background-color: $amarelo1;
        border-color: $amarelo2;

        &:hover{
            color: #fff;
            background-color: $amarelo2;
            border-color: $amarelo3;
        }
    }

    &-danger{
        color: #fff;
        background-color: $vermelho1;
        border-color: $vermelho2;

        &:hover{
            color: #fff;
            background-color: $vermelho2;
            border-color: $vermelho3;
        }
    }

    // OUTLINES ---------------------------------------------
    &-outline{
        &-primary{
            color: $azul2;
            background-color: transparent;
            border-color: $azul2;
            &:hover{
                color: #fff;
                background-color: $azul2;
                border-color: $azul2;
            }
        }

        &-secondary{
            color: $cinza5;
            background-color: transparent;
            border-color: $cinza5;
            &:hover{
                color: #fff;
                background-color: $cinza5;
                border-color: $cinza5;
            }
        }

        &-success{
            color: $verde1;
            background-color: transparent;
            border-color: $verde1;
            &:hover{
                color: #fff;
                background-color: $verde1;
                border-color: $verde1;
            }
        }

        &-info{
            color: $azul5;
            background-color: transparent;
            border-color: $azul5;
            &:hover{
                color: #fff;
                background-color: $azul5;
                border-color: $azul5;
            }
        }

        &-warning{
            color: $amarelo1;
            background-color: transparent;
            border-color: $amarelo1;
            &:hover{
                color: #fff;
                background-color: $amarelo1;
                border-color: $amarelo1;
            }
        }

        &-danger{
            color: $vermelho1;
            background-color: transparent;
            border-color: $vermelho1;
            &:hover{
                color: #fff;
                background-color: $vermelho1;
                border-color: $vermelho1;
            }
        }
    }

    // SIZES ------------------------
    &.btn-small{
        padding: 5px 9px;
        font-size: .928571rem;
        border-radius: .2rem;
    }
    &.btn-large{
        padding: 9px 18px;
        font-size: 1.285714rem;
        border-radius: .3rem;
    }
    &.btn-hero{
        padding: 14px 38px;
        font-size: 1rem;
        line-height: 16px;
        border-radius: 3px;
        height: auto;
        &.btn-small{
            padding: 11px 34px;
            font-size: 1rem;
            border-radius: 3px;
        }
        &.btn-large{
            padding: 15px 42px;
            font-size: 1.142857rem;
            border-radius: 3px;
        }
    }
    &.btn-block{
        display: block;
        width: 100%;
    }

    // VARIATIONS ---------------------------------
    &.btn-rounded{
        border-radius: 50px !important;
    }
    &.btn-noborder{
        border: 0;
    }
    &.pull-right{
        float: right;
    }
    &.pull-left{
        float: left;
    }

    // ICONS
    img{
        vertical-align: middle;
        margin-left: 5px;
    }
}

a.inline{
    text-decoration: none;
    color: $azul1;
}