.block{
    background: white;
    margin-bottom: 25px;
    border-radius: 5px;

    &-header{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 14px 18px;
        transition: opacity .2s ease-out;
    }
    &-content{
        transition: opacity .2s ease-out;
        margin: 0 auto;
        padding: 18px;
        width: 100%;
        overflow-x: visible;
    }
    &.no-round{
        border-radius: 0;
    }
}